$navbar-color: #ffb7c0;

.navbar {
    font-family: 'DancingScript-SemiBold', sans-serif;
    font-size: 20px;
  
    position: relative;
    overflow: hidden;
    height: 100px;
    border-radius: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $navbar-color;
    z-index: 1;
    border-radius: 15px;
}

.navbar .nav-brand {
    position: absolute;
    left: 80px;
    z-index: 2;
}

.nav-logo {
    height: 80px;
    width: auto;
    transition: transform 0.3s;

    &:hover {
        transform: scale(1.1);
    }
}

.navbar .nav-links {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: center;
    flex: 1;
}
  
.navbar .nav-links a {
    color: white;
    text-decoration: none;
    padding: 10px 20px;
    border-radius: 15px;
    transition: background-color 0.3s;
}
  
.navbar .nav-links a:hover {
    border-radius: 15px;
    background-color: rgba(255, 255, 255, 0.2);
}