$background-color1:#fdf4c1;
$background-color2:#e8d777;
$background-color3:#fde4c1;

@font-face {
    font-family: "DancingScript-SemiBold";
    src: url('/assets/fonts/DancingScript-SemiBold.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "字语东京街角的小浪漫";
    src: url('/assets/fonts/字语东京街角的小浪漫.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

body {
    background-image: url('/assets/images/background.png');
    background-repeat:no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    min-width: 1260px;

    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.content {
    width: 800px;
    box-sizing: border-box;

    font-family: "字语东京街角的小浪漫";
    font-size: 26px;
    color: #333333;
    background-color: $background-color1;
    border: 3px solid $background-color2;
    border-radius: 15px;
    box-shadow: 13px 13px 0 $background-color2;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);

    position: relative;
    margin: 60px auto;
    padding: 60px;

    line-height: 1.5;
}

.content::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('/assets/images/chikawa.png');
    background-repeat: no-repeat;
    background-position: calc(0% + 100px) calc(100% - 60px);
    background-size: 15%;
    z-index: 2;
}

.content p,
.content .salutation,
.content .closing {
    background-image: linear-gradient(to bottom, transparent 90%, #d3d3d3 90%, #d3d3d3 93%, transparent 93%);
    background-size: 100% 1.5em;
    background-repeat: repeat-y;
    margin: 0.1em;
    padding-left: 1em;
    padding-right: 1em;
    padding-bottom: 0.1em;
}

.content p {
    text-indent: 2em;
}

.content .salutation {
    text-align: left;
}

.content .closing {
    text-align: right;
}

// 在现有样式后添加媒体查询
@media (max-width: 768px) {
    body {
      min-width: auto; // 移除固定宽度限制
      background-size: auto 100%; // 适应竖屏比例
    }
  
    .content {
      width: 95%; // 改用百分比宽度
      // margin: 20px auto; // 减少上下边距
      padding: 30px; // 减少内边距
      font-size: 18px; // 调小字号
      // line-height: 1.4; // 调整行高
      // box-shadow: 8px 8px 0 $background-color2; // 调小阴影尺寸
  
      &::before {
        background-size: 20%; // 调小装饰图片尺寸
        background-position: calc(0% + 30px) calc(100% - 30px); // 调整装饰图片位置
      }
    }
  
    // 调整段落样式
    .content p {
      text-indent: 1em; // 减少首行缩进
      padding-left: 0.5em;
      padding-right: 0.5em;
    }
}