$bgmbar-color1: #ffb7bf;
$bgmbar-color2: #ffffff;
$nohover-opacity: 0.6;
$bg-color: #{rgba($bgmbar-color2, 0.5)};
$bgmbar-color1-nohover: #{rgba($bgmbar-color1, $nohover-opacity)};
$bgmbar-color2-nohover: #{rgba($bgmbar-color2, $nohover-opacity)};

#music-controls {
    position: fixed;
    left: -55px;
    top: 50%;
    transform: translateY(-50%);
    background-color: $bg-color;
    border: solid 2px $bgmbar-color1-nohover;
    border-radius: 12px;
    padding: 10px;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: left 0.3s ease;
}

#music-controls.hidden {
    display: none;
}

#music-controls:hover {
    left: 0;
}

#music-controls .control-icon {
    background-color: $bgmbar-color1-nohover;
    color: $bgmbar-color2;
    border: none;
    border-radius: 50%;
    padding: 10px;
    cursor: pointer;
    margin-top: 5px;
    margin-bottom: 5px;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

#music-controls .control-icon:hover {
    background-color: $bgmbar-color1;
}

/* 播放图标 */
#music-controls .control-icon.play .play-icon {
    background-image: url('../icons/play.svg');
    display: block;
}

#music-controls .control-icon.play .pause-icon {
    display: none;
}

/* 暂停图标 */
#music-controls .control-icon.pause .pause-icon {
    background-image: url('../icons/pause.svg');
    display: block;
}

#music-controls .control-icon.pause .play-icon {
    display: none;
}

/* 静音图标 */
#music-controls .control-icon.mute .mute-icon {
    background-image: url('../icons/mute.svg');
    display: block;
}

#music-controls .control-icon.mute .unmute-icon {
    display: none;
}

/* 取消静音图标 */
#music-controls .control-icon.unmute .unmute-icon {
    background-image: url('../icons/unmute.svg');
    display: block;
}

#music-controls .control-icon.unmute .mute-icon {
    display: none;
}

#music-controls .control-icon span {
    width: 24px;
    height: 24px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

#volume-bar {
    -webkit-appearance: none;
    writing-mode: vertical-rl;
    height: 100px;
    width: 8px;
    background: none;
    cursor: pointer;
    margin-top: 5px;
    margin-bottom: 5px;
    --volume-percentage: 100%;
}

#volume-bar::-webkit-slider-runnable-track {
    width: 8px;
    height: 100px;
    background: linear-gradient(to top, $bgmbar-color1-nohover 0%, $bgmbar-color1-nohover var(--volume-percentage), $bgmbar-color2-nohover var(--volume-percentage), $bgmbar-color2-nohover 100%);
    border: none;
    border-radius: 12px;
}

#volume-bar::-webkit-slider-runnable-track:hover {
    background: linear-gradient(to top, $bgmbar-color1 0%, $bgmbar-color1 var(--volume-percentage), $bgmbar-color2 var(--volume-percentage), $bgmbar-color2 100%);
}

#volume-bar::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: none;
    height: 12px;
    width: 12px;
    border-radius: 50%;
    background: $bgmbar-color2;
    margin-right: -2px;
    position: relative;
    z-index: 1;
}